xt-base), 0.4);\n\n  + .livestream__comment--hyperchat {\n    margin-bottom: var(--spacing-xxs);\n  }\n\n  .livestream-comment__body {\n    display: flex;\n    align-items: flex-start;\n    flex-direction: unset;\n    flex: unset;\n    margin-left: 3px;\n\n    .freezeframe-wrapper,\n    .channel-thumbnail--xsmall {\n      margin-left: var(--spacing-xs);\n      margin-top: var(--spacing-xxs);\n    }\n  }\n\n  .button__content {\n    color: var(--color-primary);\n  }\n\n  .date_time {\n    opacity: 1;\n  }\n  .livestream-comment__menu {\n    position: absolute;\n    top: 2px;\n    right: 6px;\n  }\n\n  &::before {\n    display: none;\n    position: absolute;\n    left: 0;\n    height: 100%;\n    max-height: 4rem;\n    border-top-right-radius: 2px;\n    border-bottom-right-radius: 2px;\n    width: 5px;\n    background-color: var(--color-hyperchat);\n    content: '';\n  }\n}\n\n.hyperchat-level1 {\n  background-color: rgba(mix(#de0050, #ffa236, 0%), 0.08);\n  .livestream-comment__hyperchat-banner {\n    background-image: linear-gradient(to right, mix(#de0050, #ffa236, 0%), transparent);\n  }\n}\n\n.hyperchat-level2 {\n  border: 1px solid mix(#de0050, #ffa236, 25%);\n  background-color: rgba(mix(#de0050, #ffa236, 25%), 0.08);\n  .livestream-comment__hyperchat-banner {\n    background-image: linear-gradient(to right, mix(#de0050, #ffa236, 25%), transparent);\n  }\n}\n\n.hyperchat-level3 {\n  border: 1px solid mix(#de0050, #ffa236, 50%);\n  background-color: rgba(mix(#de0050, #ffa236, 50%), 0.08);\n  .livestream-comment__hyperchat-banner {\n    background-image: linear-gradient(to right, mix(#de0050, #ffa236, 50%), transparent);\n  }\n}\n\n.hyperchat-level4 {\n  border: 1px solid mix(#de0050, #ffa236, 75%);\n  background-color: rgba(mix(#de0050, #ffa236, 75%), 0.08);\n  .livestream-comment__hyperchat-banner {\n    background-image: linear-gradient(to right, mix(#de0050, #ffa236, 75%), transparent);\n  }\n}\n\n.hyperchat-level5 {\n  border: 1px solid mix(#de0050, #ffa236, 100%);\n  background-color: rgba(mix(#de0050, #ffa236, 100%), 0.08);\n  .livestream-comment__hyperchat-banner {\n    background-image: linear-gradient(to right, mix(#de0050, #ffa236, 100%), transparent);\n  }\n}\n\n.livestream-comment__body {\n  display: flex;\n  align-items: flex-start;\n  margin-left: var(--spacing-s);\n  overflow: hidden;\n\n  .channel-thumbnail {\n    @include handleChannelGif(2rem);\n    margin-top: var(--spacing-s);\n    flex-shrink: 0;\n  }\n\n  .livestream-comment--minimal {\n    .channel-thumbnail {\n      margin-top: 1px;\n    }\n  }\n\n  @media (max-width: $breakpoint-small) {\n    margin-left: 0;\n  }\n}\n\n.livestream-comment__menu {\n  position: absolute;\n  right: var(--spacing-s);\n  top: var(--spacing-xxs);\n  width: 20px;\n  height: 20px;\n\n  @media (min-width: $breakpoint-small) {\n    .menu__button {\n      background: var(--color-background);\n      border-radius: 50%;\n      padding: 2px;\n      border: 2px solid transparent;\n      transition: transform 0.4s;\n      .icon {\n        width: 16px;\n        height: 16px;\n      }\n    }\n    [aria-expanded='true'].menu__button {\n      opacity: 1;\n      background-color: rgba(var(--color-header-button-base), 0.9);\n      border: 2px solid var(--color-header-background);\n      transform: rotate(90deg);\n      .icon {\n        stroke: var(--color-primary);\n      }\n    }\n  }\n}\n\n.livestream-comment__hyperchat-banner {\n  @extend .hyperChat;\n\n  border-radius: 0;\n  display: inline-block;\n  position: relative;\n  width: 90%;\n  background-image: linear-gradient(to right, rgba(var(--color-text-base), 0.4), transparent);\n  padding: 1px;\n  padding-left: 0;\n  display: flex;\n\n  .hyperChat {\n    background-color: var(--color-header-background);\n  }\n\n  .credit-amount {\n    color: var(--color-text);\n    svg {\n      height: var(--font-small);\n      margin-bottom: -2px;\n    }\n  }\n}\n\n.livestream-comment__text {\n  padding-right: var(--spacing-s);\n  padding-bottom: var(--spacing-xxs);\n\n  .markdown-preview {\n    p,\n    h1,\n    h2,\n    h3,\n    h4,\n    h5,\n    h6,\n    tr,\n    th,\n    td,\n    li {\n      word-break: break-word;\n    }\n\n    .task-list-item {\n      margin-left: var(--spacing-m);\n      input[type='checkbox'] {\n        height: 26px;\n        width: 18px;\n        margin-bottom: -8px;\n        margin-right: var(--spacing-xxxs);\n        border: 2px solid var(--color-border);\n      }\n    }\n\n    .channel-name {\n      font-size: var(--font-small);\n    }\n\n    .button--link {\n      .icon {\n        flex-shrink: 0;\n      }\n    }\n\n    .menu__button {\n      opacity: 1 !important;\n      color: var(--color-primary);\n      &:hover {\n        color: var(--color-primary);\n      }\n    }\n\n    .comment__badge {\n      display: none;\n    }\n\n    .emote {\n      max-width: 1.1rem;\n      max-height: 1.1rem;\n    }\n  }\n}\n\n.livestream__members-only-message {\n  border-radius: var(--border-radius);\n  padding: var(--spacing-xxxs) var(--spacing-xs);\n  background-image: linear-gradient(147deg, rgba(var(--color-primary-static), 0.2), rgba(242, 121, 55, 0.6));\n  border: 2px solid var(--color-primary);\n  border: width 2px;\n\n  .icon--Membership {\n    margin-right: var(--spacing-xs);\n    margin-bottom: -6px;\n    background-image: linear-gradient(147deg, rgba(var(--color-primary-static), 1), rgba(242, 121, 55, 1));\n    border-radius: 50%;\n    padding: 3px;\n    width: 24px !important;\n    height: 24px !important;\n  }\n\n  .icon--Unlock {\n    display: inline;\n    float: right;\n    width: 24px;\n    height: 24px;\n    border-radius: 50%;\n    background: var(--color-header-background);\n    color: #91f92d;\n    padding: 4px;\n    overflow: visible;\n  }\n}\n","@mixin between {\n  display: flex;\n  justify-content: space-between;\n}\n\n@mixin breakpoint-max($breakpoint) {\n  @media (max-width: #{$breakpoint}px) {\n    @content;\n  }\n}\n\n@mixin breakpoint-min($breakpoint) {\n  @media (min-width: #{$breakpoint}px) {\n    @content;\n  }\n}\n\n@mixin center {\n  align-items: center;\n  display: inline-flex;\n  justify-content: center;\n}\n\n@mixin clearfix {\n  clear: both;\n  content: '';\n  display: block;\n}\n\n// (Smart) text truncation\n// Pass in a width to customize how much text is allowed\n// Omit value for basic text truncation\n@mixin constrict($value: null) {\n  @if ($value) {\n    max-width: $value;\n  }\n\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n}\n\n@mixin create-grid($items-per-row: 4) {\n  grid-template: repeat(1, 1fr) / repeat($items-per-row, 1fr);\n}\n\n// Smart font include\n// Simply pass in the font-weight you want to use and the normal/italicized versions will be added\n// No more weighing down the front-end with references to unused weights\n@mixin font-face($font-weight, $relative-font-path, $font-name) {\n  @font-face {\n    font-family: $font-name;\n    font-style: normal;\n    font-weight: $font-weight;\n    // sass-lint:disable indentation\n    src: url('#{$relative-font-path}/#{$font-weight}.woff2') format('woff2'),\n      url('#{$relative-font-path}/#{$font-weight}.woff') format('woff');\n    // sass-lint:enable indentation\n  }\n\n  @font-face {\n    font-family: $font-name;\n    font-style: italic;\n    font-weight: $font-weight;\n    // sass-lint:disable indentation\n    src: url('#{$relative-font-path}/#{$font-weight}i.woff2') format('woff2'),\n      url('#{$relative-font-path}/#{$font-weight}i.woff') format('woff');\n    // sass-lint:enable indentation\n  }\n}\n\n@mixin font-mono {\n  font-family: 'Fira Code', 'Courier New', monospace;\n}\n\n@mixin font-sans {\n  font-family: Inter, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif,\n    'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';\n}\n\n@mixin font-serif {\n  font-family: Georgia, serif;\n}\n\n@mixin hide-text {\n  border: none;\n  color: transparent;\n  font: 0 / 0 a;\n  text-shadow: none;\n}\n\n// Cross-browser line-clamp support\n@mixin line-clamp($element-height: 2rem, $row-count: 2, $fade-color: var(--lbry-white), $computed-position: relative) {\n  height: $element-height;\n  overflow: hidden;\n  position: $computed-position;\n\n  &::after {\n    width: 50%;\n    height: calc(#{$element-height} / #{$row-count});\n    right: 0;\n    bottom: 0;\n\n    background-image: linear-gradient(to right, rgba($lbry-white, 0), #{$fade-color} 80%);\n    content: '';\n    position: absolute;\n  }\n}\n\n@mixin no-user-select {\n  user-select: none;\n\n  -ms-user-select: none;\n  -moz-user-select: none;\n  -webkit-user-select: none;\n}\n\n// Use CSS variables without upsetting Sass-Lint\n// https://github.com/sasstools/sass-lint/issues/1161#issuecomment-390537190\n@mixin root-prop($prop: null, $value: null) {\n  @if ($prop and $value) {\n    #{$prop}: $value;\n  }\n}\n\n@mixin selection($background-color: var(--lbry-white), $text-color: var(--lbry-black)) {\n  &::selection {\n    background-color: $background-color;\n    color: $text-color;\n    text-shadow: none;\n  }\n\n  &::-moz-selection {\n    background-color: $background-color;\n    color: $text-color;\n    text-shadow: none;\n  }\n}\n\n@mixin thumbnail {\n  &::before,\n  &::after {\n    content: '';\n  }\n\n  &::before {\n    float: left;\n    padding-top: var(--aspect-ratio-standard);\n  }\n\n  &::after {\n    clear: both;\n    display: block;\n  }\n}\n\n@mixin linkFocus {\n  background-color: var(--color-link-focus-bg);\n}\n\n@mixin underline($text-color: var(--lbry-black), $whitespace-color: var(--lbry-white)) {\n  @include selection($text-color, $whitespace-color);\n\n  background-image: linear-gradient($whitespace-color, $whitespace-color),\n    linear-gradient($whitespace-color, $whitespace-color), linear-gradient($text-color, $text-color);\n  background-position: 0 88%, 100% 88%, 0 88%;\n  background-repeat: no-repeat, no-repeat, repeat-x;\n  background-size: 0.05rem 1px, 0.05rem 1px, 1px 1px;\n  box-decoration-break: clone;\n  display: inline;\n  text-decoration: none;\n  text-shadow: 0.03rem 0 $whitespace-color, -0.03rem 0 $whitespace-color, 0 0.03rem $whitespace-color,\n    0 -0.03rem $whitespace-color, 0.06rem 0 $whitespace-color, -0.06rem 0 $whitespace-color, 0.09rem 0 $whitespace-color,\n    -0.09rem 0 $whitespace-color, 0.12rem 0 $whitespace-color, -0.12rem 0 $whitespace-color, 0.15rem 0 $whitespace-color,\n    -0.15rem 0 $whitespace-color;\n\n  @-moz-document url-prefix() {\n    // sass-lint:disable-line empty-args\n    background-position: 0 90%, 100% 90%, 0 90%;\n  }\n}\n\n@mixin placeholder {\n  // Temporary fix for lags in Chromium based browsers\n  //animation: pulse 2s infinite ease-in-out;\n  background-color: var(--color-placeholder-background);\n  border-radius: var(--card-radius);\n  border-width: 0;\n}\n\n.pulse {\n  // animation: pulse 2s infinite ease-in-out;\n}\n\n@mixin mediaThumbHoverZoom {\n  .media__thumb,\n  img {\n    transition: all 0.2s ease;\n  }\n\n  &:hover {\n    .media__thumb,\n    img {\n      transform: scale(1.05);\n    }\n  }\n}\n\n@mixin list-hover-effect {\n  &:hover {\n    background-color: rgba(var(--color-primary-dynamic), 0.1);\n  }\n}\n\n@mixin handleClaimTileGifThumbnail($width) {\n  .ff-canvas,\n  .freezeframe-img {\n    // height: calc(#{$width} * (9 / 16)) !important;\n    // width: $width;\n    border-bottom-left-radius: 0;\n    border-bottom-right-radius: 0;\n  }\n}\n\n@mixin handleClaimListGifThumbnail($width) {\n  .ff-canvas,\n  .freezeframe-img {\n    // height: calc(#{$width} * (9 / 16)) !important;\n    // width: $width;\n  }\n}\n\n@mixin handleChannelGif($size) {\n  height: $size;\n  width: $size;\n\n  .ff-canvas,\n  .freezeframe-img {\n    border-radius: 50%;\n    width: 100%;\n    height: 100%;\n    // height: $size !important;\n    // width: $size !important;\n  }\n}\n\n@mixin tier-restriction-checkbox {\n  display: inline-block;\n  margin-top: 0;\n  margin-right: var(--spacing-s);\n\n  label {\n    padding: var(--spacing-xxxs) var(--spacing-s);\n    border-radius: var(--border-radius);\n    &::before,\n    &::after {\n      display: none;\n    }\n  }\n\n  input[type='checkbox'] + label {\n    opacity: 0.8;\n    &:hover {\n      opacity: 1;\n    }\n  }\n  input[type='checkbox']:checked + label {\n    opacity: 1;\n  }\n\n  &:nth-of-type(1) {\n    input[type='checkbox'] + label {\n      border: 2px solid rgba(var(--color-membership-1), 0.6);\n      background-color: rgba(var(--color-membership-1), 0.2);\n      &:hover {\n        background-color: rgba(var(--color-membership-1), 0.4);\n      }\n    }\n    input[type='checkbox']:checked + label {\n      background-color: rgba(var(--color-membership-1), 1);\n    }\n  }\n  &:nth-of-type(2) {\n    input[type='checkbox'] + label {\n      border: 2px solid rgba(var(--color-membership-2), 0.6);\n      background-color: rgba(var(--color-membership-2), 0.2);\n      &:hover {\n        background-color: rgba(var(--color-membership-2), 0.4);\n      }\n    }\n    input[type='checkbox']:checked + label {\n      background-color: rgba(var(--color-membership-2), 1);\n    }\n  }\n  &:nth-of-type(3) {\n    input[type='checkbox'] + label {\n      border: 2px solid rgba(var(--color-membership-3), 0.6);\n      background-color: rgba(var(--color-membership-3), 0.2);\n      &:hover {\n        background-color: rgba(var(--color-membership-3), 0.4);\n      }\n    }\n    input[type='checkbox']:checked + label {\n      background-color: rgba(var(--color-membership-3), 1);\n    }\n  }\n  &:nth-of-type(4) {\n    input[type='checkbox'] + label {\n      border: 2px solid rgba(var(--color-membership-4), 0.6);\n      background-color: rgba(var(--color-membership-4), 0.2);\n      &:hover {\n        background-color: rgba(var(--color-membership-4), 0.4);\n      }\n    }\n    input[type='checkbox']:checked + label {\n      background-color: rgba(var(--color-membership-4), 1);\n    }\n  }\n  &:nth-of-type(5) {\n    input[type='checkbox'] + label {\n      border: 2px solid rgba(var(--color-membership-5), 0.6);\n      background-color: rgba(var(--color-membership-5), 0.2);\n      &:hover {\n        background-color: rgba(var(--color-membership-5), 0.4);\n      }\n    }\n    input[type='checkbox']:checked + label {\n      background-color: rgba(var(--color-membership-5), 1);\n    }\n  }\n  &:nth-of-type(6) {\n    input[type='checkbox'] + label {\n      border: 2px solid rgba(var(--color-membership-6), 0.6);\n      background-color: rgba(var(--color-membership-6), 0.2);\n\n      &:hover {\n        background-color: rgba(var(--color-membership-6), 0.4);\n      }\n    }\n    input[type='checkbox']:checked + label {\n      background-color: rgba(var(--color-membership-6), 1);\n    }\n  }\n}\n\n@mixin border-std {\n  border: 1px solid var(--color-border);\n  border-radius: var(--card-radius);\n}\n"],"sourceRoot":""}]);
